import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import Home from "./pages/home";
import Search from "./pages/search";
import Shopcar from "./pages/shopcar1";
import Vip from "./pages/vip";
import Newlist from "./pages/newlist";
import Newsdetail from "./pages/newsdetail";
import Photoshare from "./pages/photoshare";
import Shoplist from './pages/shoplist';
import Shopdetail from './pages/shopdetail';

export default new Router({
  routes: [
    {path:"/",redirect:'/home'},
    {path:"/home",component:Home},
    {path:"/search",component:Search},
    {path:"/shopcar1",component:Shopcar},
    {path:"/vip",component:Vip},
    {path:"/newlist",component:Newlist},
    {path:"/newsdetail/:id",component:Newsdetail},
    {path:"/photoshare",component:Photoshare},
    {path:"/shoplist",component:Shoplist},
    {path:"/shopdetail/:id",component:Shopdetail},




  ],
  linkActiveClass:'mui-active'
  
})
