<template>
    <div>
        <div class="posti">
            <div class="selview" v-for="(item,index) of datalist" :key="item.id">
                <van-checkbox v-model="checked" checked-color="#07c160" class="all1"></van-checkbox>
                    <van-card
                        :num="item.count"
                        :price="item.price"
                        :title="item.id"
                        :thumb="serve.baseurl+item.img"
                        >
                        </van-card>
                        <a href="#" class="delete" @click="deldata(item.id,index)">删除</a>
                <Num :dataId="item.count" :numId="item.id"></Num>
                
            </div>
            
        </div>
       <div class="jiesuan">
           <van-submit-bar
                :price="$store.getters.allprice*100"
                button-text="提交订单"          
            >
            <van-checkbox v-model="checked">全选</van-checkbox>
            </van-submit-bar>
       </div>
      
        <!-- @submit="onSubmit" -->
    </div>
</template>
<script>
import mui from "../libs/mui/js/mui.min.js";
import Num from "../components/Numberbox"
export default {
    methods: {
        deldata(id,i){
            this.datalist.splice(i,1);
            this.$store.commit("deletData",id);
            // console.log(this.datalist)
        }
    },
    components:{
        Num,
    },
    data() {
        return {
            checked: true,
            datalist:[],      
        }
  },
  created() {
      this.datalist=this.$store.getters.getdataall
  },
  mounted() {
       mui("mui-numbox").numbox();     
        // console.log(this.datalist)
    },
}
</script>
<style scoped>
.selview{position: relative;}
.posti{margin-bottom: 110px;}
.van-card__thumb{margin-left:20px;;}
.all1{position:absolute;top:40px;z-index: 1;left: 10px;}
.van-card{background:#fafafa}
.mui-numbox{position: absolute;top:70px;left: 130px;width: 126px;
height: 24px;}
.van-submit-bar{bottom: 56px;padding-left: 10px;}
.jiesuan{background: #fafafa!}
.delete{position: absolute;z-index: 999;top:75px;left: 300px;;}


</style>