import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
// console.log(Vuex);
// data=JSON.parse(localStorage.getItem("car"))
let store=new Vuex.Store({
    state:{
        data:localStorage.getItem('car')?JSON.parse(localStorage.getItem('car')):[],
    },
    mutations:{
        getData(state,obj){
            var temp=true;
                state.data.forEach((item)=>{
                    if(item.id==obj.id){
                        item.count=parseInt(item.count)+parseInt(obj.count)-parseInt(item.count);
                        temp=false;
                    }
                })
                if(temp){
                    state.data.push(obj);
                }
                console.log(JSON.stringify(state.data));
                localStorage.setItem('car',JSON.stringify(state.data));    
        },
        change(state,obj){
            state.data.forEach((item)=>{
                if(item.id==obj.id){
                    item.count=obj.num;
                }
                return true;
            })
            localStorage.setItem('car',JSON.stringify(state.data));
        },
        //删除操作
        deletData(state,id){
            state.data.forEach((item,i)=>{
                if(item.id==id){
                    state.data.splice(i,1);
                }
            })
            localStorage.setItem('car',JSON.stringify(state.data));
        }
    },
    getters:{
        allnumver:function(state){
            var addnum=0;
            
            state.data.forEach((item)=>{
               addnum+=parseInt(item.count);
            })
            return addnum;
        },
        getdataall:function(state){
            return state.data;
        },
        allprice:function(state){
            var all=0;
            state.data.forEach((item)=>{
                all+=item.price*item.count
            })
            return all;
        }

    }
});
export default store;