<script>
//滑动效果
import mui from "../libs/mui/js/mui.min.js";
import photosharelist from "./photosharelist";
export default{
	methods: {
		list(data){
			// console.log(this.listid)
			return this.listid=data;
			console.log(this.listid)
			
		},
		listOne(data){
