			url:this.serve.baseurl+"api/photoshare",
			params:{_page:1,_limit:10},
		}).then((res)=>{
			console.log(res.data.data);
			res.data.data.unshift({title:"推荐",detail:res.data.data[1].detail})//这里有一个小问题
			this.datashare=res.data.data;
			
		}).then(()=>{
			this.listOne(this.datashare[0].detail);//每点击前的预先加载
