<template>
<div>
     <mt-button icon="back" @click="fanhui"></mt-button>
     <div class="newdetail">
        
        <h4 class="title">{{detailDate.title}}</h4>
        <p class="subtitle">
            <span>发布时间：{{detailDate.add_time}}</span>
            <span>点赞：{{detailDate.click}}次</span>
        </p>
        <hr>
        <div class="contents" v-html="detailDate.content">
           
        </div>
        
        <Comment></Comment>
        
        
        
    </div>
</div>

   
</template>
<script>
import Comment from "../components/comment.vue"
export default {
    methods: {
      fanhui(){
          this.$router.go(-1);
      }
  },
    components:{
        Comment,
    },
    data() {
        return {
            detailDate:{}
        }
    },
    created() {
        let id=this.$route.params.id
        // console.log(id);
        axios({
            // url:`http://localhost:3000/api/newlist/${id}`
            url:this.serve.baseurl+'api/newlist/'+id,
        }).then((res)=>{
            this.detailDate=res.data.data;
            // console.log(this.detailDate)
        })
    },
}
</script>
<style scoped>
   .newdetail{padding:0 10px 0 10px;}
   .newdetail .title{color: red}
   .newdetail .subtitle{display: flex;justify-content: space-between;color:#26a2ff}
   .newdetail .contents{color: #333;font-size: 16px;text-indent: 2rem;}

</style>>
    
