<template>
    
    <div class="shopdetail">
        <div class="shopimg">
             <img :src="serve.baseurl+goodsData.url_img" alt="">
        </div>
        <div class="goods">
                <div class="title1">{{goodsData.title}}</div>
                <div class="price">
                    <span>市场价：￥{{goodsData.oldprice}}</span>
                    <span class="te">销售价：￥<i>{{goodsData.newprice}}</i></span>
                </div>
                <div class="mui-content-padded">
                    购买数量:
                    <div class="mui-numbox " data-numbox-min='0' data-numbox-max='10'>
                        <button class="mui-btn mui-btn-numbox-minus" type="button">-</button>
                        <input class="mui-input-numbox" type="number" @change="addshopcar" ref="number"/>
                        <button class="mui-btn mui-btn-numbox-plus" type="button">+</button>
                    </div>
                </div>
                <div class="btn">
                     <mt-button type="primary" size="normal" class="hhh">立即购买</mt-button>
                     <mt-button type="danger" size="normal" class="hhh" @click="sendcar">加入购物车</mt-button>
                </div>
                <div class="other">
                    <div class="big">商品参数</div>
                    <div class="small">商品编号：{{goodsData.number}}</div>
                    <div class="small">库存情况：{{goodsData.cout}}件</div>
                </div>				
			
        </div> 
    </div>
</template>
<script>
import mui from "../libs/mui/js/mui.min.js";

export default {
    methods: {
        addshopcar(){
            this.shopdata={
                id:this.goodsData.title,
                count:this.$refs.number.value,
                price:this.goodsData.newprice,
                img:this.goodsData.url_img,
                select:true
            }
            console.log(this.shopdata.id)  
        },
        sendcar(){
            //发送数据给mutations
            // console.log(this.shopdata);
            this.$store.commit('getData',this.shopdata);
        }
    },
    mounted() {
        // mui(".mui-numbox").numbox();
        mui(".mui-numbox").numbox().setValue(1);
        
        
       
    },
    data() {
        return {
            goodsData:{},
            shopdata:{},
        }
    },
    created() {
        
        let id=this.$route.params.id;
        axios({
            // url:`http://localhost:3000/api/shoplist/${id}`,
            url:this.serve.baseurl+"api/shoplist/"+id,
        }).then((res)=>{
            this.goodsData=res.data.data;
            // console.log(res.data.data)
            this.addshopcar();//进入页面运行一次
        })
    },
}
</script>
<style scoped>
.shopdetail{width: 100%;margin-bottom: 56px;}
.shopdetail .shopimg{height: 350px;width: 100%;border-bottom: 2px solid #cccccc}
.shopdetail .shopimg img{height: 100%;width:100%;}
.shopdetail .goods{margin-top: 20px;}
.shopdetail .goods .title1{font:700 25px/36px "";color: red;padding:0 10px 20px 10px;}
.shopdetail .goods .price{margin-top: 10px;padding-left: 10px;}
.shopdetail .goods .price span{margin-right: 10px;}
.shopdetail .goods .price .te i{color: red;font-style: normal;}
.shopdetail .goods .btn{padding-left: 10px;margin-top: 20px;}
.shopdetail .goods .btn .hhh{margin-right: 10px;}
.shopdetail .other{margin-top: 10px;border-top: 2px solid #cccccc;padding-left: 10px;padding-top: 10px
}
.shopdetail .other .big{font-size: 25px;margin-bottom: 15px;}
.shopdetail .other .small{font-size: 15px;color: #999999;}

</style>
