<template>
    <div class="comment">
        <h3>发表评论</h3>
        <hr>
        <textarea placeholder="输入评论内容（不超过200字）" maxlength="200" v-model="view"></textarea>
        <mt-button type="primary" size="large" @click.native="getView">发表评论</mt-button>
        <div class='comment-list' v-for="(item,index) of allView" :key="index">
            <div class='comment-user'>
                第{{index+1}}楼&nbsp;用户&nbsp;&nbsp;匿名用户&nbsp;发表时间:{{time|data|addzero}}
            </div>
            <div class='comment-body' v-html="item">计算机的付款的的撒发生范德萨范德萨发大水发射点发射点</div>

        </div>
        <mt-button size="large" plain>加载更多</mt-button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            view:'',
            allView:[],
            time:'',
        }
    },
    methods: {
        getView(){
           this.allView.push(this.view);
           this.time=new Date();
            
        }
    },
}
</script>
<style scoped>
    .comment{margin-top: 70px;}
    .comment-list{margin-top: 5px;}
    .comment-user{background:#ccc;font-size: 16px;}
    .comment-body{font:16px "";padding: 5px;}
    
</style>