<template>
    <div>
        <div class="mui-numbox" data-numbox-min='0' data-numbox-max='10'>
            <button class="mui-btn mui-btn-numbox-minus" type="button">-</button>
            <input class="mui-input-numbox" type="text" :value="dataId" @change="newNum(numId)" ref="numbox"/>
            <button class="mui-btn mui-btn-numbox-plus" type="button">+</button>
        </div> 
    </div>
</template>
<script>
import mui from "../libs/mui/js/mui.min.js";
export default {
    props:["dataId","numId"],
    mounted() {
        mui(".mui-numbox").numbox();
    },
    methods: {
        newNum(id){
            var num=this.$refs.numbox.value
            this.$store.commit("change",{id,num})
            // console.log(num,res);
        }
    },
}
</script>
<style scoped>
.mui-numbox{position: absolute;top:70px;left: 130px;width: 126px;
height: 24px;}
</style>